import './App.css';
import { useState } from 'react';
import {Button, Container, Nav, Navbar} from 'react-bootstrap';
import data from './db/fruit';
import data2 from "./db/veggie";
import Products from './components/Products';
import { Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom';
import Detail from './components/Detail';
import Title from './components/Title';
import Title2 from './components/Title2';
import ComVeggie from './components/ComVeggie';
import Footer from './components/Footer';
import axios from 'axios';
import Cart from './components/Cart';
import Board from './components/Board';

function App() {
  let [fruit, setFruit] = useState(data);
  let [veggie, setVeggie] = useState(data2);
  let [count, setCount] = useState(1);
  let [input, setInput] = useState("");

  let Navigate = useNavigate();
  console.log(fruit[0].price); 

  const sortByName = () => {
    let sortedFruit = [...fruit].sort((a, b) => (a.title > b.title ? 1 : -1));
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };

  const sortByPriceLowToHigh = () => {
    let sortedFruit = [...fruit].sort((a, b) => a.price - b.price);
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };

  const sortByPriceHighToLow = () => {
    let sortedFruit = [...fruit].sort((a, b) => b.price - a.price);
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };

  
  return (
    <div className="App">
      <>
        <Navbar bg="dark" data-bs-theme="dark">
          <Container>
            <Navbar.Brand
              onClick={() => {
                Navigate("/");
              }}
            >
              
            </Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link
                onClick={() => {
                  Navigate("/");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  Navigate("/detail/1");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  Navigate("/cart");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  Navigate("/about");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  Navigate("/board");
                }}
              >
                
              </Nav.Link>
            </Nav>
          </Container>
        </Navbar>

        <Routes>
          <Route
            path="/"
            element={
              <div>
                <div className="slider"></div>
                <Title />

                <div className="container">
                  <div className="row">
                    <div className="col-lg-3 col-md-6 mb-2">
                      {/*   */}
                      <input
                        placeholder=" "
                        //    input  
                        onChange={(e) => setInput(e.target.value)}
                        value={input}
                        style={{
                          width: "100%",
                          padding: "10px",
                          marginLeft: "10px",
                          borderRadius: "4px",
                          border: "1px solid #ccc",
                          marginRight: "10px",
                        }}
                      />
                    </div>
                    <div className="col-lg-3 offset-lg-6 col-md-6 mb-2">
                      {/* select */}
                      <select
                        onChange={(e) => {
                          if (e.target.value === "low") sortByPriceLowToHigh();
                          if (e.target.value === "high") sortByPriceHighToLow();
                          if (e.target.value === "name") sortByName();
                        }}
                        style={{
                          width: "100%",
                          padding: "10px",
                          marginLeft: "10px",
                          borderRadius: "4px",
                          border: "1px solid #ccc"
                        }}
                      >
                        <option value=""> </option>
                        <option value="low"> </option>
                        <option value="high"> </option>
                        <option value="name"></option>
                      </select>
                    </div>
                  </div>
                </div>

                <div className="container" style={{ marginTop: "30px" }}>
                  <div className="row">
                    {/* 'input'      */}
                    {/* .filter(...)      
                      .map(...)        */}
                    {fruit
                      .filter(
                        (item) =>
                          item.title
                            .toLowerCase()
                            .includes(input.toLowerCase()), //  
                      )
                      .map((ele, i) => (
                        <Products fruit={ele} key={ele.id} /> //   
                      ))}
                  </div>
                </div>

                <div className="container">
                  <div className="row">
                    <div style={{ textAlign: "center" }}>
                      <Title2 />
                      <div className="container" style={{ marginTop: "30px" }}>
                        <div className="row">
                          {veggie.map((ele, i) => {
                            return (
                              <ComVeggie
                                veggie={veggie[i]}
                                key={veggie[i].id}
                              />
                            );
                          })}
                        </div>
                      </div>
                      <Button
                        variant="outline-success"
                        count={count}
                        onClick={() => {
                          if (count == 1) {
                            axios
                              .get(
                                "https://thdprincess.github.io/react_data/veggie2.json",
                              )
                              .then((result) => {
                                let veggie2 = [...veggie, ...result.data];
                                setVeggie(veggie2);
                                setCount(count + 1);
                              })
                              .catch((error) => {
                                console.error(
                                  "Error loading veggie2.json",
                                  error,
                                );
                              });
                          } else if (count == 2) {
                            axios
                              .get(
                                "https://thdprincess.github.io/react_data/veggie3.json",
                              )
                              .then((result) => {
                                let veggie3 = [...veggie, ...result.data];
                                setVeggie(veggie3);
                                setCount(count + 1);
                              })
                              .catch((error) => {
                                console.error(
                                  "Error loading veggie3.json",
                                  error,
                                );
                              });
                          }

                          if (count === 3) {
                            alert("  .");
                          }
                        }}
                      >
                        {" "}
                        + 3   {" "}
                      </Button>{" "}
                    </div>
                  </div>
                </div>

                <Footer />
              </div>
            }
          />
          <Route path="/detail/:paramId" element={<Detail fruit={fruit} />} />
          <Route path="/cart" element={<Cart />} />

          <Route path="/about" element={<About />}>
            <Route path="member" element={<Member />} />
            <Route path="location" element={<Location />} />
          </Route>

          <Route path="/board" element={<Board />} />
        </Routes>
      </>
    </div>
  );
}


function About(){
  return (
    <>
      <h4></h4>
      <Outlet></Outlet>
    </>
  )
}

function Member(){
  return (
    <>
      <h4>Member</h4>
    </>
  )
}

function Location(){
  return (
    <>
      <h4>Location</h4>
    </>
  )
}

export default App;
