import React from "react";

const Footer = () => {
  let bottom = {
    color: "#fff",
    backgroundColor: "#000",
    padding: "20px 0px",
    marginTop: "80px",
    marginBottom: "0"
  };
  return (
    <>
      <p style={bottom}>COPYRIGHT(C) 2025 , Inc. All Rights Reserved</p>
    </>
  );
};

export default Footer;
